
<template>
  <div id="appreport_pegawai">
    <form @submit.prevent="submit">
      <div class="row justify-content-center" v-if="btable">
        <div class="col-10 p-3 shadow rounded">
          <div v-if="forms">
            <p class="p-3 text-xl font-semibold">Form Table report_pegawai :</p>
            <hr class="my-2" />

            <!-- <div class="sm-form">
              <label for="id">id</label>
              <input
                disabled
                type="Disabled"
                id="id"
                name="id"
                class="form-control form-control-sm"
                placeholder="id"
                v-model="vdata['id']"
              />
            </div> -->

            <div class="row">
              <div class="col-sm-6">
                <div class="sm-form">
                  <label for="tanggal">tanggal</label>
                  <input
                    required
                    type="Date"
                    id="tanggal"
                    name="tanggal"
                    class="form-control form-control-sm"
                    placeholder="tanggal"
                    v-model="vdata['tanggal']"
                  />
                </div>
                <div class="row">
                    <div class="col-6">
                        <div class="sm-form ">
                            <label for="time_dari">Time dari : </label>
                            <input type="time" id="time_dari" name="time_dari" class="form-control form-control-sm" placeholder="time_dari" v-model="vdata.time_dari" >
                        </div>
                    </div>
                    <div class="col-6">
                        <div class="sm-form ">
                            <label for="time_sampai">Time sampai : </label>
                            <input type="time" id="time_sampai" name="time_sampai" class="form-control form-control-sm" placeholder="time_sampai" v-model="vdata.time_sampai" >
                        </div>
                    </div>
                </div>

                <div class="sm-form">
                  <label for="subject">subject</label>
                  <input
                    required
                    type="Text"
                    id="subject"
                    name="subject"
                    class="form-control form-control-sm"
                    placeholder="subject"
                    v-model="vdata['subject']"
                  />
                </div>

                <p>customer_id :</p>
                    <v-select :options="relationship.customer" label="customer_name" v-model="vdata.customer_id" :reduce="e => e['customer_name']"></v-select>
                <p>assigned_id :</p>
                    <v-select :options="relationship.assigned" label="assigned_name" v-model="vdata.assigned_id" :reduce="e => e['assigned_name']"></v-select>
                <p>tags_id :</p>
                    <v-select :options="relationship.tags" label="tags_name" v-model="vdata.tags_id" :reduce="e => e['tags_name']"></v-select>
              </div>
              <div class="col-sm-6">
                <p>stage_id :</p>
                    <v-select :options="relationship.stage" label="stage_name" v-model="vdata.stage_id" :reduce="e => e['stage_name']"></v-select>
                <div class="sm-form">
                  <label for="description">description</label>
                  <div class="sm-form">
                    <textarea
                      required
                      type="text"
                      id="description"
                      name="description"
                      rows="2"
                      placeholder="description"
                      v-model="vdata['description']"
                      class="form-control md-textarea"
                    ></textarea>
                  </div>
                </div>

                <p>helpdesk_id :</p>
                    <v-select :options="relationship.helpdesk" label="helpdesk_name" v-model="vdata.helpdesk_id" :reduce="e => e['helpdesk_name']"></v-select>
                <p>ticket_type_id :</p>
                    <v-select :options="relationship.ticket_type" label="ticket_type" v-model="vdata.ticket_type_id" :reduce="e => e['ticket_type']"></v-select>
              </div>
            </div>

            <hr class="style2" />
            <!-- BUTTON FORM -->
            <div class="my-3">
              <button
                type="submit"
                class="btn btn-sm capitalize"
                :class="
                  btn == 'tambah'
                    ? 'btn-primary'
                    : btn == 'update'
                    ? 'btn-success'
                    : 'btn-danger'
                "
              >
                <span class="typcn typcn-info"></span> @{{ btn }}
              </button>
              <div class="float-right">
                <button
                  type="button"
                  v-if="btn != 'tambah'"
                  @click="
                    btn = 'tambah';
                    vdata = {};
                  "
                  class="btn btn-sm btn-dark"
                >
                  <span class="typcn typcn-edit"></span> Refresh
                </button>
                <button
                  type="button"
                  v-if="btn != 'tambah'"
                  @click="btn = 'delete'"
                  class="ml-3 btn btn-sm btn-danger"
                >
                  <span class="typcn typcn-delete"></span> Delete
                </button>
              </div>
            </div>
          </div>
          <div v-if="btable">
              <p class="text-xs font-semibold text-red-400">Tambahkan list customer, assigned, tags, stage, helpdesk, ticket_type di menu kiri aplikasi</p>
            <div class="p-3">
              <button
                type="button"
                @click="forms = !forms"
                class="btn btn-sm btn-success"
              >
                {{ forms ? "Hide" : "Show" }} Form
              </button>
              <div class="row">
                  <div class="col-6">
                      <p>Bulan : </p>
                        <select class='form-control' v-model="filterBulan">
                            <option value="01">Januari</option>
                            <option value="02">Februari</option>
                            <option value="03">Maret</option>
                            <option value="04">April</option>
                            <option value="05">Mei</option>
                            <option value="06">Juni</option>
                            <option value="07">Juli</option>
                            <option value="08">Agustus</option>
                            <option value="09">September</option>
                            <option value="10">Oktober</option>
                            <option value="11">November</option>
                            <option value="12">Desember</option>
                        </select>
                  </div>
                  <div class="col-6">
                    <div class="sm-form ">
                        <label for="filterTahun">Tahun : </label>
                        <select class='form-control' v-model="filterTahun">
                        <option v-for="(item, index) in 50" :key="index+'ok'">{{2020+item}}</option>
                        </select>
                    </div>
                  </div>
              </div>
            </div>
            <Btable
              :tables="td"
              :hide="[
                'upload',
                'id',
                'created_at',
                'updated_at',
                'deleted_at',
                'file',
                'gambar',
                'description',
                'time_dari',
                'time_sampai'
              ]"
              @selected="ambil"
              @hapusdata="hapusData"
              @hapussemuadata="hapusSemuaData"
              @pdf="pdf"
              @excel="excel"
            />
          </div>
        </div>
      </div>
      <div v-else>
          <div style="height:60vh;" class="d-flex justify-content-center align-items-center shadow-lg rounded-lg">
              <div style="width:100%">
                  <p class="text-center animate__animated animate__bounce animate__infinite text-lg font-semibold ">Loading ...</p>
              </div>
          </div>
      </div>
    </form>
  </div>
</template>
<script type="module">
import Btable from "@/components/Btables";
import autophp from "@/plugins/autophp";
let sdb = new autophp();
import firebase from "firebase";
let db = firebase.firestore();
export default {
  layout: "hit-mobile",
  data() {
    return {
      vdata: {},
      btn: "tambah",
      filterBulan: "01",
      filterTahun: "2022",
      content: "",
      datanya: [],
      relationship: {},
      btable: false,
      forms: false,
    };
  },
  components: {
    Btable: Btable,
  },
  watch:{
      filterBulan(){
        this.btable=false;
        this.$forceUpdate();
        setTimeout(() => {
            this.btable=true;
            this.$forceUpdate();
        }, 500);
      },
      filterTahun(){
        this.btable=false;
        this.$forceUpdate();
        setTimeout(() => {
            this.btable=true;
            this.$forceUpdate();
        }, 500);
      },
      btable(){
          
      }
  },
  computed:{
      td(){
          let that=this;
          let data=this.datanya;
        
          data=data.filter(e=>{
              if(e.tanggal.split('-')[0].indexOf(this.filterTahun)!=-1){
                  return e;
              }
          })
          data=data.filter(e=>{
              if(e.tanggal.split('-')[1].indexOf(this.filterBulan)!=-1){
                  return e;
              }
          })
          return data;
      }
  },
  methods: {
    async getAlldata(data) {
      let datas = await db
        .collection(`${data}`)
        .get();
      datas = datas.docs.map((doc) => {
        return { id: doc.id, ...doc.data() };
      });
      this.relationship[data] = datas;
      return datas;
    },
    pdf() {},
    excel() {
        window.open(`https://akbarmaliki.github.io/infolayanansjs/#/excel?data=${JSON.stringify(this.datanya).split('&').join('%26')}`, '_blank');
    },
    hapusData() {
      let that = this;
      if (confirm("Apakah hapus data ?")) {
        db.collection(
          "users/" + this.$store.state.users.uid + "/report_pegawai"
        )
          .doc(this.vdata.id)
          .delete()
          .then((res) => {
            that.getData();
            sdb.alert("Delete Data Berhasil", "bg-green-400");
          })
          .catch((err) => {
            sdb.alert("Delete Data Gagal", "bg-red-400");
          });
      }
    },
    hapusSemuaData(data) {
      let that = this;
      if (confirm("Apakah hapus Semua data ?")) {
        data.forEach((e) => {
          db.collection(
            "users/" + this.$store.state.users.uid + "/report_pegawai"
          )
            .doc(e.id)
            .delete()
            .then((res) => {
              sdb.alert("Delete Data Berhasil", "bg-green-400");
            })
            .catch((err) => {
              sdb.alert("Delete Data Gagal", "bg-red-400");
            });
        });
        setTimeout(() => {
          that.getData();
        }, 2000);
      }
    },
    upload(id) {
      let file = document.querySelector(`#${id}`); // berikan id pada input file
      file = document.querySelector("#filenya"); // berikan id pada input file
      sdb
        .collection("tbuser", false)
        .upload(file, false, true) // file,compress,scramble
        .then((res) => {
          this.vdata[`${id}`] = res;
          this.$forceUpdate();
          sdb.alert("Upload Data Berhasil", "bg-green-400");
        });
    },
    ambil(data) {
      this.vdata = JSON.parse(JSON.stringify(data));
      this.btn = "update";
      this.forms = true;
      this.$forceUpdate();
    },
    getData() {
      let that = this;
      that.btable = false;
      db.collection("users/" + this.$store.state.users.uid + "/report_pegawai")
        .get()
        .then((res) => {
          let data = res.docs.map((doc) => {
            return {
              id: doc.id,
              ...doc.data(),
            };
          });
           data = data.map((e) => {
            return {
              id: e.id,
              tanggal: e.tanggal,
              time_dari: e.time_dari,
              time_sampai: e.time_sampai,
              subject: e.subject,
              customer_id: e.customer_id,
              assigned_id: e.assigned_id,
              tags_id: e.tags_id,
              stage_id: e.stage_id,
              description: e.description,
              helpdesk_id: e.helpdesk_id,
              ticket_type_id: e.ticket_type_id,
            };
          });
          data= _.orderBy(data, ['tanggal'],['asc']);
          that.datanya = data;
          that.btable = true;
          that.$forceUpdate();
          sdb.alert("Get Data Berhasil", "bg-green-400");
        })
        .catch((err) => {
          that.btable = true;
          sdb.alert("Get Data Gagal", "bg-red-400");
        });
    },
    submit() {
      let that = this;
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          db.collection(
            "users/" + this.$store.state.users.uid + "/report_pegawai"
          )
            .doc()
            .set(this.vdata, { merge: true })
            .then((res) => {
              that.getData();
              sdb.alert("Add Data Berhasil", "bg-green-400");
            })
            .catch((err) => {
              sdb.alert("Add Data Gagal", "bg-red-400");
            });
        } else if (this.btn == "update") {
          db.collection(
            "users/" + this.$store.state.users.uid + "/report_pegawai"
          )
            .doc(this.vdata.id)
            .set(this.vdata, { merge: true })
            .then((res) => {
              that.getData();
              sdb.alert("Update Data Berhasil", "bg-green-400");
            })
            .catch((err) => {
              sdb.alert("Update Data Gagal", "bg-red-400");
            });
        } else if (this.btn == "delete") {
          db.collection(
            "users/" + this.$store.state.users.uid + "/report_pegawai"
          )
            .doc(this.vdata.id)
            .delete()
            .then((res) => {
              that.getData();
              sdb.alert("Delete Data Berhasil", "bg-green-400");
            })
            .catch((err) => {
              sdb.alert("Delete Data Gagal", "bg-red-400");
            });
        }
      }
    },
     getTahun(tgl) {
        return this.$datefns.format(
            new Date(tgl),
            "YYYY"
        );
    },
     getBulan(tgl) {
        return this.$datefns.format(
            new Date(tgl),
            "MM"
        );
    },
  },
  mounted() {
      this.filterTahun=this.getTahun(new Date())
      this.filterBulan=this.getBulan(new Date())
      setTimeout(() => {
          this.getData();
      
          this.getAlldata("customer");
      
          this.getAlldata("assigned");
      
          this.getAlldata("tags");
      
          this.getAlldata("stage");
      
          this.getAlldata("helpdesk");
      
          this.getAlldata("ticket_type");
      }, 1000);
  },
};
</script>
